.a {
  height: 100vh;
  display: flex;
  align-items: center;
}

.a-left {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  height: 100%;
}

.a-right {
  flex: 1;
}

.a-card.bg {
  position: absolute;
  top: 50px;
  left: 50px;
  background-color: #333;
}

.a-card {
  width: 60%;
  height: 70vh;
  border-radius: 30px;
  position: relative;
  overflow: hidden;
}

.a-img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.a-web-img {
  width: 155px;
  height: 120px;
  position: absolute;
  border-radius: 20px;
  bottom: 70px;
}

.a-title {
  font-weight: 400;
}

.a-sub {
  margin: 20px 0px;
  margin-right: 45px;
}

.a-desc {
  font-weight: 300;
  margin-right: 45px;
}

.a-web-desc {
  position: absolute;
  bottom: 50px;
  width: 550px;
}

.a-web {
  margin-top: 50px;
  display: flex;
  position: relative;
  align-items: center;
  justify-content: space-between;
}

.a-web-texts {
  width: 75%;
  margin-bottom: 150px;
}

.a-web-title {
  font-weight: bold;
  color: #555;
  position: absolute;
  top: 50px;
  left: 1px;
}

.a-web-img2 {
  height: 75px;
}

@media screen and (max-width: 480px) {
  .a {
    flex-direction: center;
    text-align: center;
    margin-top: 50px;
  }

  .a-left {
    width: 100%;
  }

  .a-card {
    height: 30vh;
  }

  .a-card.bg,
  .a-web {
    display: none;
  }

  .a-right {
    padding: 20px;
  }
}
